import cuPanel from '@/components/custom/cu-panel.vue'
import cuCell from '@/components/custom/cu-cell.vue'
import cuCellGroup from '@/components/custom/cu-cell-group.vue'
import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
import { post,tokenpost} from '@/api/user.js'
import { api } from '@/config/common.js'
export default {
	components: {
		cuPanel,
		cuCell,
		cuCellGroup,
		uniList,
		uniListItem
	},
	data() {
		return {
			reqData: {
				companylogourl:'',
				companyname:'湖北吉奥汽车服务有限公司',
				contact: '',
				telephone: '',
				wx:'',
				email:'',
				address:''
			},
			title: '设置'
		};
	},
	onShow() {
		this.loadData();
	},
	methods: {
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		loadData(){
			tokenpost(api.GetUserInfo).then(res => {
				if (res.status == 200 && res.data.returnCode == '0000') {
				  this.dataList = res.data.data
				} else {
					this.$api.msg(res.data.returnMessage) 
				}
				this.loading =false;
			}).catch(error => {
				this.loading =false;
				this.$api.msg('请求失败fail') 
			})
		},
		handleSubmit() {
			const {companylogourl,companyname,contact,telephone,email,address} = this;
			const sendData = {
				companylogourl,
				companyname,
				contact,
				telephone,
				email,
				address
			};
			tokenpost(api.SaveUserInfo,sendData).then(res => {
				if (res.status == 200 && res.data.returnCode == '0000') {
				  this.$api.msg(res.data.returnMessage) 
				}else if(res.status == 200 && res.data.returnCode == '402'){
					this.$api.msg(res.data.returnMessage);
					uni.reLaunch({
						url:'/pages/my/login/login'
					})
				}else {
					this.$api.msg(res.data.returnMessage) 
				}
				this.loading =false;
			}).catch(error => {
				this.loading =false;
				this.$api.msg('请求失败fail') 
			})
			
		}
	}
}
